@import url(https://fonts.googleapis.com/css?family=Rubik);
@import url(https://fonts.googleapis.com/css?family=Montserrat);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  font-family: 'Rubik', sans-serif;
}

.app {
  display: flex;
  flex-direction: column;
  height: 100vh;
  margin: 0;
  overflow: hidden;
  text-align: center;
  width: 100vw;
}

.header {
  align-items: center;
  background: white;
  border-bottom: 1px solid #E1ECF5;
  display: flex;
  font-weight: bold;
  height: 60px;
  text-transform: uppercase;
}

.header .title {
  color: #9CB0C0;
  margin-left: 5em;
}

.content {
  margin: 2em 5em;
  text-align: left;
}

p {
  color: #777;
  font-size: 1.2em;
  font-weight: 400;
  margin-bottom: 1.5em;
}

#calendar {
  margin: 20px;
}

.month {
  /*margin-right: 8px;*/
}

.month-name {
  fill: #777;
}

.tooltip {
  background: white;
  border-radius: 10px;
  border: solid 1px #e8e8e8;
  display: block;
  font-size: 85%;
  font-weight: bold;
  line-height: 30px;
  opacity: .8;
  overflow: hidden;
  padding: 10px 20px;
  position: fixed;
}

#calendar svg {
  height: auto;
  width: auto;
}

#calendar g {
  font-size: 75%;
  line-height: 1.5em;
  position: absolute;
  top: 260px;
}

#calendar .day {
  cursor: pointer;
}

.dayLabels {
  color: #777;
  display: block;
}

.months {
  margin-left: 40px;
}

.tooltip .text {
  font-weight: 400 !important;
}

#calendar {
  width: calc(100vw - 100px);
  display: inline-block;
}

.overlay {
  text-align: center;
  position: relative;
  z-index: 4;
  font-size: 40px;
  top: 60px;
  color: purple;
  width: calc(100vw - 40px);
}

.hoveredMonths {
  -webkit-filter: blur(3px);
          filter: blur(3px);
}

.yearLabel {
  font-size: 1.2em;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: calc(100vw - 200px);
  border: solid 1px #eaeaea;
  border-radius: 40px;
  margin-bottom: 24px;
  color: #777;
}

.fa-chevron-left {
  margin-left: 1.2em;
}

.fa-chevron-right {
  margin-right: 1.2em;
}

.hidden {
  display: none;
}

.yearLabel i {
  cursor: pointer;
}

